import React from "react";
import TextField from "@material-ui/core/TextField";
import Autocomplete from "@material-ui/lab/Autocomplete";

export default function CountryCodePicker(props) {

    const [value, setValue] = React.useState(countryCodes[102]);

    React.useEffect(() => {

        if (props.disabled == false) {

            //if there is already a value supplied, use that
            if (props.value) {
               countryCodes.forEach((v) => {
                    if (v.code == props.value) {
                        setValue(v);
                    }
                })

            } else {
                props.valueChanged(value.code);
            }


        }


    }, [])

    /*React.useEffect(() => {
        if(props.disabled==false){
            props.valueChanged(value.code);
        }

    }, [value])*/

    React.useEffect(() => {

        countryCodes.forEach((v) => {
            if (v.code == props.value) {
                setValue(v);
                return
            }
        })

    }, [props.value])


    return (

        <Autocomplete
            id="ctry-code-box"
            options={countryCodes}
            disabled={props.disabled}
            value={value}
            onChange={(event, newValue) => {
                if (newValue == null) {
                    return
                }
                setValue(newValue);
                props.valueChanged(newValue.code);
            }}
            getOptionLabel={(option) => option.country}
            style={{width: "80%"}}
            renderInput={(params) => <TextField {...params} margin={"dense"} label="Country Code"
                                                variant="standard"/>}
        />
    );


}


